<template>
  <div
    draggable="true"
    class="bg-white dark:bg-n-solid-3 p-4 rounded-lg border border-n-weak cursor-move hover:shadow-lg transition-all duration-200 hover:scale-[1.02]"
    @dragstart="$emit('dragstart', $event)"
  >
    <!-- Header do Card -->
    <div class="flex items-start justify-between mb-3">
      <div class="flex items-center gap-2">
        <span class="px-2 py-0.5 text-xs font-medium text-green-700 bg-green-100 dark:bg-green-900/30 dark:text-green-400 rounded">
          {{ contact.id }}
        </span>
        <span
          class="w-2 h-2 rounded-full"
          :class="{
            'bg-red-500': contact.priority === 'high',
            'bg-yellow-500': contact.priority === 'medium',
            'bg-blue-500': contact.priority === 'low',
          }"
        ></span>
      </div>
      
      <!-- Menu Dropdown -->
      <div class="relative">
        <button 
          @click.stop="toggleMenu"
          class="text-n-slate-10 hover:text-n-slate-12 transition-colors p-1 hover:bg-n-solid-4 rounded"
        >
          <svg class="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
            <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M12 5v.01M12 12v.01M12 19v.01M12 6a1 1 0 110-2 1 1 0 010 2zm0 7a1 1 0 110-2 1 1 0 010 2zm0 7a1 1 0 110-2 1 1 0 010 2z" />
          </svg>
        </button>

        <!-- Dropdown Menu -->
        <div
          v-if="showMenu"
          v-on-click-outside="closeMenu"
          class="absolute right-0 mt-1 w-48 bg-white dark:bg-n-solid-4 rounded-lg shadow-lg border border-n-weak z-50"
        >
          <div class="py-1">
            <button
              @click="viewDetails"
              class="w-full px-4 py-2 text-left text-sm text-n-slate-12 hover:bg-n-solid-3 transition-colors flex items-center gap-2"
            >
              <svg class="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M15 12a3 3 0 11-6 0 3 3 0 016 0z" />
                <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M2.458 12C3.732 7.943 7.523 5 12 5c4.478 0 8.268 2.943 9.542 7-1.274 4.057-5.064 7-9.542 7-4.477 0-8.268-2.943-9.542-7z" />
              </svg>
              Ver Detalhes
            </button>
            <button
              @click="editCard"
              class="w-full px-4 py-2 text-left text-sm text-n-slate-12 hover:bg-n-solid-3 transition-colors flex items-center gap-2"
            >
              <svg class="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M11 5H6a2 2 0 00-2 2v11a2 2 0 002 2h11a2 2 0 002-2v-5m-1.414-9.414a2 2 0 112.828 2.828L11.828 15H9v-2.828l8.586-8.586z" />
              </svg>
              Editar
            </button>
            <button
              @click="duplicateCard"
              class="w-full px-4 py-2 text-left text-sm text-n-slate-12 hover:bg-n-solid-3 transition-colors flex items-center gap-2"
            >
              <svg class="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M8 16H6a2 2 0 01-2-2V6a2 2 0 012-2h8a2 2 0 012 2v2m-6 12h8a2 2 0 002-2v-8a2 2 0 00-2-2h-8a2 2 0 00-2 2v8a2 2 0 002 2z" />
              </svg>
              Duplicar
            </button>
            <hr class="my-1 border-n-weak" />
            <button
              @click="deleteCard"
              class="w-full px-4 py-2 text-left text-sm text-red-600 hover:bg-red-50 dark:hover:bg-red-900/20 transition-colors flex items-center gap-2"
            >
              <svg class="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M19 7l-.867 12.142A2 2 0 0116.138 21H7.862a2 2 0 01-1.995-1.858L5 7m5 4v6m4-6v6m1-10V4a1 1 0 00-1-1h-4a1 1 0 00-1 1v3M4 7h16" />
              </svg>
              Excluir
            </button>
          </div>
        </div>
      </div>
    </div>

    <!-- Título -->
    <h3 class="font-semibold text-n-slate-12 mb-2 line-clamp-2">
      {{ contact.title }}
    </h3>

    <!-- Descrição -->
    <p class="text-sm text-n-slate-11 mb-4 line-clamp-2">
      {{ contact.description }}
    </p>

    <!-- Footer do Card -->
    <div class="flex items-center justify-between pt-3 border-t border-n-weak">
      <div class="flex items-center gap-2">
        <div class="w-6 h-6 rounded-full bg-gradient-to-br from-blue-500 to-purple-600 flex items-center justify-center text-white text-xs font-medium">
          {{ contact.avatar }}
        </div>
        <span class="text-xs text-n-slate-11">{{ contact.assignee }}</span>
      </div>
      
      <div class="flex items-center gap-1 text-n-slate-11">
        <svg class="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
          <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M7 8h10M7 12h4m1 8l-4-4H5a2 2 0 01-2-2V6a2 2 0 012-2h14a2 2 0 012 2v8a2 2 0 01-2 2h-3l-4 4z" />
        </svg>
        <span class="text-xs font-medium bg-red-500 text-white px-1.5 py-0.5 rounded-full">
          {{ contact.comments }}
        </span>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref } from 'vue';
import { vOnClickOutside } from '@vueuse/components';

const props = defineProps({
  contact: {
    type: Object,
    required: true,
  },
});

const emit = defineEmits(['dragstart', 'view-details']);

const showMenu = ref(false);

const toggleMenu = () => {
  showMenu.value = !showMenu.value;
};

const closeMenu = () => {
  showMenu.value = false;
};

const viewDetails = () => {
  closeMenu();
  emit('view-details', props.contact);
};

const editCard = () => {
  closeMenu();
  console.log('Editar card:', props.contact.id);
};

const duplicateCard = () => {
  closeMenu();
  console.log('Duplicar card:', props.contact.id);
};

const deleteCard = () => {
  closeMenu();
  console.log('Excluir card:', props.contact.id);
};
</script>