<template>
  <Teleport to="body">
    <Transition name="modal">
      <div
        v-if="isOpen"
        class="fixed inset-0 z-50 flex items-center justify-center p-4 bg-black/50"
        @click.self="closeModal"
      >
        <div class="bg-white dark:bg-n-solid-3 rounded-xl shadow-2xl max-w-2xl w-full max-h-[90vh] overflow-hidden">
          <!-- Header -->
          <div class="flex items-center justify-between p-6 border-b border-n-weak">
            <div class="flex items-center gap-3">
              <div class="w-10 h-10 rounded-full bg-gradient-to-br from-blue-500 to-purple-600 flex items-center justify-center text-white font-medium">
                {{ contact?.avatar }}
              </div>
              <div>
                <h2 class="text-xl font-semibold text-n-slate-12">{{ contact?.title }}</h2>
                <p class="text-sm text-n-slate-11">{{ contact?.id }}</p>
              </div>
            </div>
            <button
              @click="closeModal"
              class="text-n-slate-10 hover:text-n-slate-12 transition-colors p-2 hover:bg-n-solid-4 rounded-lg"
            >
              <svg class="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M6 18L18 6M6 6l12 12" />
              </svg>
            </button>
          </div>

          <!-- Body -->
          <div class="p-6 overflow-y-auto max-h-[calc(90vh-180px)]">
            <!-- Prioridade -->
            <div class="mb-6">
              <label class="block text-sm font-medium text-n-slate-11 mb-2">Prioridade</label>
              <div class="flex items-center gap-2">
                <span
                  class="px-3 py-1 rounded-full text-xs font-medium"
                  :class="{
                    'bg-red-100 text-red-700 dark:bg-red-900/30 dark:text-red-400': contact?.priority === 'high',
                    'bg-yellow-100 text-yellow-700 dark:bg-yellow-900/30 dark:text-yellow-400': contact?.priority === 'medium',
                    'bg-blue-100 text-blue-700 dark:bg-blue-900/30 dark:text-blue-400': contact?.priority === 'low',
                  }"
                >
                  {{ priorityLabel }}
                </span>
              </div>
            </div>

            <!-- Responsável -->
            <div class="mb-6">
              <label class="block text-sm font-medium text-n-slate-11 mb-2">Responsável</label>
              <div class="flex items-center gap-3 p-3 bg-n-solid-2 rounded-lg">
                <div class="w-8 h-8 rounded-full bg-gradient-to-br from-blue-500 to-purple-600 flex items-center justify-center text-white text-sm font-medium">
                  {{ contact?.avatar }}
                </div>
                <span class="text-n-slate-12 font-medium">{{ contact?.assignee }}</span>
              </div>
            </div>

            <!-- Descrição -->
            <div class="mb-6">
              <label class="block text-sm font-medium text-n-slate-11 mb-2">Descrição</label>
              <p class="text-n-slate-12 p-4 bg-n-solid-2 rounded-lg">
                {{ contact?.description }}
              </p>
            </div>

            <!-- Informações Adicionais -->
            <div class="grid grid-cols-2 gap-4 mb-6">
              <div class="p-4 bg-n-solid-2 rounded-lg">
                <div class="flex items-center gap-2 text-n-slate-11 mb-1">
                  <svg class="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M7 8h10M7 12h4m1 8l-4-4H5a2 2 0 01-2-2V6a2 2 0 012-2h14a2 2 0 012 2v8a2 2 0 01-2 2h-3l-4 4z" />
                  </svg>
                  <span class="text-sm">Comentários</span>
                </div>
                <p class="text-2xl font-semibold text-n-slate-12">{{ contact?.comments }}</p>
              </div>
              
              <div class="p-4 bg-n-solid-2 rounded-lg">
                <div class="flex items-center gap-2 text-n-slate-11 mb-1">
                  <svg class="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M12 8v4l3 3m6-3a9 9 0 11-18 0 9 9 0 0118 0z" />
                  </svg>
                  <span class="text-sm">Criado em</span>
                </div>
                <p class="text-sm font-medium text-n-slate-12">{{ createdDate }}</p>
              </div>
            </div>

            <!-- Timeline / Atividades -->
            <div class="mb-6">
              <label class="block text-sm font-medium text-n-slate-11 mb-3">Atividades Recentes</label>
              <div class="space-y-3">
                <div class="flex gap-3 p-3 bg-n-solid-2 rounded-lg">
                  <div class="w-8 h-8 rounded-full bg-blue-500 flex items-center justify-center text-white flex-shrink-0">
                    <svg class="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                      <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M12 6v6m0 0v6m0-6h6m-6 0H6" />
                    </svg>
                  </div>
                  <div>
                    <p class="text-sm text-n-slate-12 font-medium">Card criado</p>
                    <p class="text-xs text-n-slate-11">há 2 dias</p>
                  </div>
                </div>
                
                <div class="flex gap-3 p-3 bg-n-solid-2 rounded-lg">
                  <div class="w-8 h-8 rounded-full bg-green-500 flex items-center justify-center text-white flex-shrink-0">
                    <svg class="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                      <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M8 12h.01M12 12h.01M16 12h.01M21 12c0 4.418-4.03 8-9 8a9.863 9.863 0 01-4.255-.949L3 20l1.395-3.72C3.512 15.042 3 13.574 3 12c0-4.418 4.03-8 9-8s9 3.582 9 8z" />
                    </svg>
                  </div>
                  <div>
                    <p class="text-sm text-n-slate-12 font-medium">{{ contact?.assignee }} adicionou um comentário</p>
                    <p class="text-xs text-n-slate-11">há 1 dia</p>
                  </div>
                </div>

                <div class="flex gap-3 p-3 bg-n-solid-2 rounded-lg">
                  <div class="w-8 h-8 rounded-full bg-purple-500 flex items-center justify-center text-white flex-shrink-0">
                    <svg class="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                      <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M7 7h.01M7 3h5c.512 0 1.024.195 1.414.586l7 7a2 2 0 010 2.828l-7 7a2 2 0 01-2.828 0l-7-7A1.994 1.994 0 013 12V7a4 4 0 014-4z" />
                    </svg>
                  </div>
                  <div>
                    <p class="text-sm text-n-slate-12 font-medium">Prioridade alterada para {{ priorityLabel }}</p>
                    <p class="text-xs text-n-slate-11">há 3 horas</p>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <!-- Footer -->
          <div class="flex items-center justify-end gap-3 p-6 border-t border-n-weak bg-n-solid-2">
            <button
              @click="closeModal"
              class="px-4 py-2 text-sm font-medium text-n-slate-11 hover:text-n-slate-12 transition-colors"
            >
              Fechar
            </button>
            <button
              class="px-4 py-2 text-sm font-medium text-white bg-blue-600 hover:bg-blue-700 rounded-lg transition-colors"
            >
              Editar Card
            </button>
          </div>
        </div>
      </div>
    </Transition>
  </Teleport>
</template>

<script setup>
import { computed } from 'vue';

const props = defineProps({
  isOpen: {
    type: Boolean,
    default: false,
  },
  contact: {
    type: Object,
    default: null,
  },
});

const emit = defineEmits(['close']);

const closeModal = () => {
  emit('close');
};

const priorityLabel = computed(() => {
  const labels = {
    high: 'Alta',
    medium: 'Média',
    low: 'Baixa',
  };
  return labels[props.contact?.priority] || 'Média';
});

const createdDate = computed(() => {
  const date = new Date();
  date.setDate(date.getDate() - 2);
  return date.toLocaleDateString('pt-BR', { 
    day: '2-digit', 
    month: 'short', 
    year: 'numeric' 
  });
});
</script>

<style scoped>
.modal-enter-active,
.modal-leave-active {
  transition: opacity 0.3s ease;
}

.modal-enter-from,
.modal-leave-to {
  opacity: 0;
}

.modal-enter-active .bg-white,
.modal-leave-active .bg-white {
  transition: transform 0.3s ease;
}

.modal-enter-from .bg-white,
.modal-leave-to .bg-white {
  transform: scale(0.9);
}
</style>